/* Estilo para la sección de experiencia y la línea de tiempo */
.experience {
  padding: 4rem 10%;
  text-align: center;
}

.timeline {
  position: relative;
  max-width: 800px;
  margin: 2rem auto;
  text-align: left;
}

/* La línea vertical de la timeline */
.timeline::after {
  content: '';
  position: absolute;
  width: 4px;
  background-color: var(--primary-color);
  top: 0;
  bottom: 0;
  left: 50%;
  margin-left: -2px;
}

.timelineItem {
  padding: 1rem 2rem;
  position: relative;
  width: 50%;
}

/* Alineación de los items */
.timelineItem:nth-child(odd) {
  left: 0;
  padding-right: 40px; /* Espacio para la línea */
}

.timelineItem:nth-child(even) {
  left: 50%;
  padding-left: 40px; /* Espacio para la línea */
}

/* Círculo en la línea */
.timelineItem::after {
  content: '';
  position: absolute;
  width: 16px;
  height: 16px;
  background-color: var(--background-color);
  border: 4px solid var(--primary-color);
  border-radius: 50%;
  top: 24px;
  z-index: 1;
}

.timelineItem:nth-child(odd)::after {
  right: -8px;
}

.timelineItem:nth-child(even)::after {
  left: -8px;
}

.timelineContent {
  padding: 1.5rem;
  background-color: var(--surface-color);
  border-radius: 8px;
  position: relative;
}

.timelineContent h3 {
  margin-top: 0;
}

.techList {
  margin-top: 1rem;
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.techTag {
  background-color: var(--primary-color);
  color: var(--background-color);
  padding: 0.3rem 0.6rem;
  border-radius: 4px;
  font-size: 0.8rem;
  font-weight: 700;
}