.about {
  padding: 4rem 10%;
  background-color: var(--surface-color);
  text-align: center;
}

.container {
  display: flex;
  justify-content: space-around;
  text-align: left;
  gap: 2rem;
  margin-top: 2rem;
}

.column {
  width: 45%;
}

.tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-top: 1rem;
}

.tag {
  background-color: var(--background-color);
  padding: 0.5rem 1rem;
  border-radius: 5px;
  font-size: 0.9rem;
  border: 1px solid var(--primary-color);
}